import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Tab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="店铺"
                        key="dianpu"
                        icon={
                            <i className='iconfont icon-dianpu'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-dianpu1'></i>                           
                        }
                        selected={ pathname === '/' }
                        onPress={ () => {
                            this.props.history.push('/');
                        } }
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-kefu'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-kefu1'></i>                           
                        }
                        title="客服"
                        key="kefu"
                        selected={ pathname === '/' }
                        onPress={ () => {
                            this.props.history.push('/')
                        } }
                        data-seed="logId1"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-shoucang'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-shoucang1'></i>                           
                        }
                        title="收藏"
                        key="shoucang"
                        selected={ pathname === '/' }
                        onPress={ () => {
                            this.props.history.push('/')
                        } }
                        data-seed="logId1"
                    >
			        </TabBar.Item>
                    {/* <button>加入购物车</button>
                    <button>立即购买</button> */}
                    <TabBar title="加入购物车">
                    </TabBar>
                    <TabBar.Item title="立即购买">
                    </TabBar.Item>
                </TabBar>
                
            </div>
        );
    }
}
export default withRouter(Tab);