import React from 'react'
import Tab from './Tab'
const Detail2 = () => {
    return (
        <div>
            <img src={require('../assets/detail2.jpg')} style={{width:'100%',position:'absolute'}} />
            {/* absolute是相对于浏览器定位 */}
            <button
                onClick={() => {window.location.href='/'}}
                style={{
                    width:'30px',
                    height:'30px',
                    background:'rgba(0, 0, 0, 0.4)',
                    color:'#fff',
                    fontSize:'20px',
                    borderRadius:'100%',
                    position:'relative',    //relative是相对于前面容器定位
                    margin:'15px'
                }}
            ><span>&lt;</span></button>
            <Tab />
        </div> 
    )
}
export default Detail2;